@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{padding:0;margin:0;font-family:"Kumbh Sans",sans-serif;font-weight:bold;font-size:18px;background-color:transparent}.container{width:100vw;height:100vh;display:flex;align-items:center;justify-content:center;background-color:#19a2ae}.container::before{content:'<div class="image-back"></div>';position:absolute;background-image:url(/profile-card-component-main/static/media/bg-pattern-top.dae77b1f.svg);width:1000px;height:1000px;background-repeat:round;left:-500px;top:-500px}.container::after{content:' ';position:fixed;background-image:url(/profile-card-component-main/static/media/bg-pattern-bottom.a34eb319.svg);width:1000px;height:1000px;background-repeat:round;right:-500px;bottom:-500px}.card{display:flex;flex-direction:column;align-items:center;background-color:#FFF;border-radius:15px;box-shadow:0px 0px 15px 0px rgba(0,0,0,0.5)}h2,p{text-align:center}.card__footer{width:100%;display:flex;justify-content:space-evenly;margin-bottom:30px;padding-top:30px}.card__line{width:100%;height:2px;background-color:#969696;opacity:0.5;margin-top:30px}.card__item{display:flex;flex-direction:column;text-align:center}.card__photo{position:absolute;margin-top:85px}.card__photo>img{border-radius:100px;border:3px solid #FFF}.card__content{margin-top:50px}.card__pattern>img{border-top-left-radius:15px;border-top-right-radius:15px}p,span.years,span.text{color:#969696;font-weight:normal;font-weight:initial}span.text{font-size:15px}@media (min-width: 0px) and (max-width: 600px){.container::before{background-image:none;width:0;height:0}.container::after{background-image:none;width:0;height:0}.content{display:flex;justify-content:center}.card{width:85%}.card__pattern>img{width:100%}.card__photo{margin-top:65px}}

